dropdown-menu {\n                a {\n                    color: inherit;\n                    text-decoration: none !important;\n                }\n            }\n\n            .kanban-item-title .ti {\n                float: none;\n\n                &:last-of-type {\n                    margin-right: 5px;\n                }\n            }\n\n            padding: 5px 10px 0;\n            font-weight: 600;\n\n            a:hover {\n                text-decoration: underline;\n            }\n\n            a {\n                padding: unset;\n            }\n        }\n\n        .kanban-item-team {\n            display: flex;\n            padding-bottom: 5px;\n            margin-left: 10px;\n            margin-top: 5px;\n\n            @media (prefers-reduced-motion: no-preference) {\n                &:hover {\n                    margin-right: 0;\n\n                    > span {\n                        margin-right: -5px;\n\n                        &:last-of-type {\n                            margin-right: 0;\n                        }\n                    }\n                }\n            }\n\n            @media (prefers-reduced-motion) {\n                margin-right: 0;\n\n                > span {\n                    margin-right: -10px !important;\n                    &:last-of-type {\n                        margin-right: -5px !important;\n                    }\n                }\n            }\n\n            > span {\n                margin-right: -15px;\n                border-radius: 50%;\n                border: 3px solid var(--tblr-card-bg);\n                box-sizing: content-box;\n                min-height: 20px;\n\n                img {\n                    border-radius: 50%;\n                }\n\n                &.fa-stack, & .fa-stack {\n                    width: 2em;\n                }\n            }\n        }\n\n        .kanban-item-due-date {\n            margin: 10px 10px 10px 0;\n        }\n    }\n\n    .kanban-column {\n        margin-right